<template>
  <div>
    <systemParameter></systemParameter>
  </div>
</template> 

<script>
import { addSystemParameter } from '@/service/api/systemParameter'
import systemParameter from './modules/systemParameter'
export default {
  components: {
    systemParameter
  },
  data () {
    return {
      e1: 1,
      addlist: {}, //一般资料
      addlist2: {}//模板
    }
  },
  mounted () {

  },
  methods: {
    submit (val) {
      // console.log(this.addlist, val);
      this.addlist = { function_uuid: "002", module_uuid: "002", is_used: 1, ...this.addlist }
      addSystemParameter({ ...this.addlist, ...val }).then(res => {
        if (res.code === '200') {
          this.$store.commit('newsTips', this.$t('message_added_successfully'))
          this.$router.push({
            name: 'emailtemplate'
          })
        } else {
          this.$store.commit('newsTips', res.msg)
        }
      })
    }
  }
}
</script>